.App {
    color: #333;
    margin: 30px auto;
    padding: 40px;
    background-color: rgba(255,255,255,0.75);
    min-height: calc(100vh - 60px);
    border-radius: 20px;
    width: calc(100% - 10px);
}

.App-Header {
    display: flex;
    align-items: flex-end;
    color: #333;
}

.App-Title {
    font-size: 2em; 
}

